import React, { useContext } from 'react'
import '../cssFiles/hireme.scss'
import { GoMail } from 'react-icons/go'
import { MdCall } from 'react-icons/md'
import { FaReact } from 'react-icons/fa';
import Footer from './Footer';
import { LangContext } from '../App'

function HireMe() {
    const passingData = useContext(LangContext)


    return (
        passingData ?
            <section className="wrapper-hireme" id="HireMe" >
                <h3>{passingData.contact.Contact}</h3>
                <div className="text-wrapper">
                    <div className="text" >
                        {passingData.contact.Contacttext}
                    </div>
                    <div className="line-contact"></div>
                    <span>{passingData.contact.Contactme}</span>
                    <div className="contact-cards">

                        <div className="card-mail-phone"  >
                            <a href="mailto:erik.zabransky@gmail.com"><GoMail size={45} className="iconHireMe" /><span>erik.zabransky@gmail.com</span></a>
                        </div>

                        <div className="card-mail-phone" >
                            <MdCall size={45} className="iconHireMe" />
                            <span>
                                +421 918 506 305
                        </span>
                        </div>
                    </div>
                </div>
                <div className="reactLogoWrapper" data-aos="zoom-in" data-aos-delay="500" data-aos-duration="2000" ><FaReact className="bigReactLogo" />
                </div>


                <Footer />
            </section>
            : null
    )
}

export default HireMe
