import React, { useContext } from 'react'
import '../cssFiles/myprojects.scss'
import laptopHotel from '../images/laptopHotel.webp'
import covidlaptop from '../images/covidlaptop.webp'
import businesscard from '../images/businesscard.webp'
import posterHotel from '../images/posterHotel.webp'
import { AiOutlineDoubleRight } from "react-icons/ai";
import { AiFillGithub } from "react-icons/ai";
import { LangContext } from '../App'

function MyProjects() {
    const passingData = useContext(LangContext)



    return (
        passingData ?
            <section className="myProjects-section" id="MyProjects">
                <h3 >{passingData.myprojects.Myprojects}</h3>

                <div className="project-wrapper">


                    <div className="projects-content">
                        <div className="leftDivText" >
                            <h4 >{passingData.myprojects.Covidtracker}</h4>
                            <div className="gitLink" onClick={() => window.open('https://github.com/ErkoNz/production4', '_blank')}>
                                <AiFillGithub /></div>
                            <span className="date">{passingData.myprojects.Coviddate}</span>
                            <div className="textProject">
                                {/* A small & simple Frontend JavaScript app, built with ReactJS, ChartJS, Fetching... */}
                                {passingData.myprojects.Covidtext}
                            </div>
                            <div className="builtWith">
                                <span>HTML</span>
                                <span>|</span>
                                <span>CSS</span>
                                <span>|</span>
                                <span>ReactJS</span>
                                <span>|</span>
                                <span>ChartJS</span>
                            </div>
                        </div>
                        <a href="https://erkonz.github.io/production4/#/" target="_blank" rel="noopener noreferrer">
                            <div className="rightDivImg" data-aos="zoom-in" >
                                <img src={covidlaptop} alt="" />
                                <button className="preview_button">{passingData.myprojects.Previewbutton}<div className="icon_prev_button"><AiOutlineDoubleRight /></div> </button>
                            </div>
                        </a>
                    </div>




                    <div className="projects-content">
                        <div className="leftDivText"  >
                            <h4>{passingData.myprojects.Hotelweb}</h4>
                            <span className="date">{passingData.myprojects.Hotelwebdate}</span>
                            <div className="textProject">
                                {passingData.myprojects.Hoteltext}
                            </div>
                            <div className="builtWith">
                                <span>HTML</span>
                                <span>|</span>
                                <span>CSS</span>
                                <span>|</span>
                                <span>JS</span>
                                <span>|</span>
                                <span>PHP</span>
                                <span>|</span>
                                <span>MySQL</span>
                            </div>
                        </div>
                        <a href="http://ishotelbc2.epizy.com/zabransky/uvod.php" target="_blank" rel="noopener noreferrer">
                            <div className="rightDivImg" data-aos="zoom-in" >
                                <img src={laptopHotel} alt="" />
                                <button className="preview_button">{passingData.myprojects.Previewbutton}
                                    <div className="icon_prev_button"><AiOutlineDoubleRight /></div> </button>
                            </div>
                        </a>
                    </div>



                    <div className="projects-content">
                        <div className="leftDivText"  >
                            <h4>{passingData.myprojects.Businesscard}</h4>
                            <span className="date">{passingData.myprojects.Businesscarddate}</span>
                            <div className="textProject">
                                {passingData.myprojects.Businesscardtext}
                            </div>
                            <div className="builtWith">
                                <span>Photoshop</span>
                                <span>|</span>
                                <span>CorelDraw</span>
                            </div>
                        </div>
                        <div className="instead_of_a" >
                            <div className="rightDivImg dont_blur" data-aos="zoom-in"  >
                                <img src={businesscard} alt="" />
                            </div>
                        </div>
                    </div>






                    <div className="projects-content">
                        <div className="leftDivText" >
                            <h4>{passingData.myprojects.Grillposter}</h4>
                            <span className="date">{passingData.myprojects.Grillposterdate}</span>
                            <div className="textProject">
                                {passingData.myprojects.Grilltext}
                            </div>
                            <div className="builtWith">
                                <span>Photoshop</span>
                                <span>|</span>
                                <span>CorelDraw</span>
                            </div>
                        </div>
                        <div className="instead_of_a" >
                            <div className="rightDivImg dont_blur" data-aos="zoom-in">
                                <img src={posterHotel} alt="" style={{ width: '50%' }} />
                            </div>
                        </div>
                    </div>



                </div>
            </section>
            : null
    )
}

export default MyProjects
