  const FetchIt = async () => {
    const response = await fetch('/apif')
    if (response.statusText === "OK") {
      let data = await response.json()
      setLangData(data[0])
      if (localStorage.getItem(LOCAL_STORAGE_KEY) === "SK")
        setPassingData(data[0].sk)
      else
        setPassingData(data[0].en)
    }
    else {
      var json = require('./api.json');
      setLangData(json[0])
      if (localStorage.getItem(LOCAL_STORAGE_KEY) === "SK")
        setPassingData(json[0].sk)
      else
        setPassingData(json[0].en)
    }
  }

  function ChangeLang(prop) {
    if (prop === "SK")
      setPassingData(langData.sk)
    else
      setPassingData(langData.en)
  }

  AOS.init({
    duration: 1200,
    once: true,
  })



  // if (window.screen.width < 900) {
  //   AOS.init({
