import React, { useContext } from 'react'
import '../cssFiles/mainscreen.scss'
import githubIcon from '../images/githubsvg.svg'
import myPic from '../images/myPic.webp'
import wave from '../images/wave.svg'
import { LangContext } from '../App'

function MainScreen() {
    const passingData = useContext(LangContext)

    function scrollTo(anchor) {
        var elmnt = document.getElementById(anchor);
        const offset = 25;
        const bodyRect = document.body.getBoundingClientRect().top;
        const elementRect = elmnt.getBoundingClientRect().top;
        const elementPosition = elementRect - bodyRect;
        const offsetPosition = elementPosition - offset;
        window.scrollTo({
            top: offsetPosition,
            behavior: 'smooth'
        });
    }

    return (
        passingData ?
            <div className="mainPage" id="Home" >
                <div className="waves" ><img src={wave} alt="" /></div>
                <div className="main-content" >
                    <div className="my-stuff" data-aos="fade-right">
                        <div>{passingData.mainscreen.Hi}</div>
                        <div>{passingData.mainscreen.Fullstack}<span style={{ color: '#229e56' }}>r</span></div>
                        <div>
                            {passingData.mainscreen.Iam}
                        </div>
                        <div className="button-wrapper">
                            <button onClick={() => scrollTo("MyProjects")} className="my-projects-button">{passingData.mainscreen.Myprojectsbutton}</button>
                            <button onClick={() => scrollTo("About")} className="about-button">{passingData.mainscreen.Aboutbutton}</button>
                        </div>

                        <div className="line-underButtons"></div>
                        <a href="https://github.com/ErkoNz" rel="noopener noreferrer" target="_blank"> <img src={githubIcon} alt="github" style={{ width: '50px', padding: '10px' }} /></a>
                    </div>
                    <div className="myImage-main" data-aos="zoom-in" data-aos-delay="300" >
                        <img src={myPic} alt="myPic" />
                    </div>
                </div>
            </div >
            : null
    )
}

export default MainScreen
