import React, { useState, useContext } from 'react'
import '../cssFiles/navbar.scss'
import MyLogo from '../images/MyLogo.svg'
import ScrollTo from './smallerComponents/ScrollTo'
import BurgerFunction from './smallerComponents/BurgerFunction'
import CloseBurger from './smallerComponents/CloseBurger'
import { LangContext } from '../App'

const LOCAL_STORAGE_KEY = 'local_language'

function Nav({ ChangeLang }) {
    const passingData = useContext(LangContext)
    const [coloring, setColoring] = useState(false)
    const [burgerClick, setBurgerClick] = useState({
        isOpen: false,
        styling: {
            right: "-50%"
        }
    })

    window.onload = function () {
        const lstorage = localStorage.getItem(LOCAL_STORAGE_KEY)
        let pom3
        pom3 = document.getElementById('switch');

        setTimeout(function () {
            if (!pom3) {
                pom3 = document.getElementById('switch')
                if (lstorage === "EN") {
                    setColoring(false)
                    if (pom3) pom3.checked = false
                }
                else if (lstorage === "SK") {
                    setColoring(true)
                    if (pom3) pom3.checked = true
                }
            }
        }, 300);


        setTimeout(function () {
            if (!pom3) {
                pom3 = document.getElementById('switch')
                if (lstorage === "EN") {
                    setColoring(false)
                    if (pom3) pom3.checked = false
                }
                else if (lstorage === "SK") {
                    setColoring(true)
                    if (pom3) pom3.checked = true
                }
            }
        }, 400);


        setTimeout(function () {
            if (!pom3) {
                pom3 = document.getElementById('switch')
                if (lstorage === "EN") {
                    setColoring(false)
                    if (pom3) pom3.checked = false
                }
                else if (lstorage === "SK") {
                    setColoring(true)
                    if (pom3) pom3.checked = true
                }
            }
        }, 600);


        if (lstorage === "EN") {
            setColoring(false)
            if (pom3) pom3.checked = false
        }
        else if (lstorage === "SK") {
            setColoring(true)
            if (pom3) pom3.checked = true
        }

    }

    window.onscroll = function () {
        var header = document.querySelector('.nav-wrapper')
        if (header)
            header.classList.toggle('sticky', window.scrollY > 0)
    };

    const closingBurger = () => {
        document.removeEventListener("click", closingBurger, false);     // Fails
        CloseBurger(burgerClick, setBurgerClick)
    }
    document.addEventListener('click', closingBurger, false)

    function ChangeColoring() {
        if (coloring) {
            ChangeLang("EN")
            localStorage.setItem(LOCAL_STORAGE_KEY, "EN")
            setColoring(false)
        }
        else {
            localStorage.setItem(LOCAL_STORAGE_KEY, "SK")
            ChangeLang("SK")
            setColoring(true)
        }
    }

    return (
        passingData ?
            <>
                <nav className="nav-wrapper" id="navbar" >
                    <div id="myName" className="myName-as-logo" onClick={() => ScrollTo("Home")}>
                        <div className="logoImg">
                            <img src={MyLogo} alt="EZ Logo" />
                        </div>
                        <div className="nameText">
                            ERIK<br />ZÁBRANSKÝ
                        </div>
                    </div>
                    <ul className="nav-links" id="navLinks" style={burgerClick.styling}>
                        <li onClick={() => ScrollTo("MyProjects")}>{passingData.nav.MyProjects}</li>
                        <li onClick={() => ScrollTo("About")}>{passingData.nav.About}</li>
                        <li onClick={() => ScrollTo("HireMe")}>{passingData.nav.Contact}</li>

                        <li className="toggleButton" >
                            <span className="before" style={{ color: coloring ? 'black' : 'green' }}>EN</span>
                            <input type="checkbox" id="switch" />
                            <label htmlFor="switch" onClick={() => ChangeColoring()}></label>
                            <span className="after" style={{ color: coloring ? 'green' : 'black' }}>SK</span>
                        </li>
                    </ul>

                    <div className="burgerDiv" onClick={() => BurgerFunction(burgerClick, setBurgerClick)}>

                        <div className="line1" id="IdLine1"></div>
                        <div className="line2" id="IdLine2"></div>
                        <div className="line3" id="IdLine3"></div>
                    </div>
                </nav>
            </>
            : null
    )
}

export default Nav
